@import "../assets/scss/_variables.scss";

@media only screen and (max-width: 600px) {
  .logo-grid {
    display: none;
  }
  .logo-mobile-grid {
    display: inline-block !important;
    span#logo-mobile {
      font-size: 20px;
    }
  }
}

header {
  width: 100%;
  padding: 20px 0px;
  background-color: black;

  .logo-mobile-grid {
    display: none;
  }

  span#logo,
  span#logo-mobile {
    display: block;
    font-weight: bold;
    margin-top: 8px;
    padding-left: 20px;
    padding-right: 20px;
    color: $main-blue;
  }

  button#blog-btn {
    color: $main-blue;
    border: none;
    box-shadow: none;
    font-weight: bold;
    background-color: transparent;
  }
}
