@import "../../assets/scss/variables";

.experience-container {
  margin-bottom: 100px;
  .tech-container {
    border-bottom: 6px solid $main-blue;
    p {
      text-align: center;
      text-transform: capitalize;
    }
  }
  button.snack-message {
    color: #fff;
    padding: 0;
    margin: 0;
    font-size: 16px;
    span {
      color: $main-blue;
    }
    span.snack-message-span {
      color: #fff;
      text-transform: lowercase;
    }
    b {
      color: $main-blue;
      font-weight: bold;
      text-transform: uppercase;
    }
  }
}
