@import "../../assets/scss/variables.scss";

@media only screen and (max-width: 599px) {
  .container {
    padding-top: 10vh !important;
    h2 {
      font-size: 16px !important;
    }
    h1#landing-name {
      font-size: 32px !important;
    }
    h3 {
      font-size: 14px !important;
      .chip {
        font-size: 14px !important;
      }
    }
    .photo-container {
      text-align: center;
      img {
        width: 50%;
        margin: auto;
      }
    }
    .resume-btn-container {
      width: 100% !important;
      margin-bottom: 30px;
    }
  }
}

@media screen and (min-width: 600px) and (max-width: 900px) {
  .container {
    padding-top: 20vh !important;
  }
}

@media screen and (min-width: 900px) and (max-width: 1000px) {
  .container {
    padding-top: 20vh !important;
  }
}

@media screen and (min-width: 1000px) and (max-width: 1200px) {
  .container {
    padding-top: 15vh !important;
  }
}

@media only screen and (min-width: 600px) and (max-width: 1200px) {
  h1#landing-name {
    font-size: 40px !important;
  }
  .photo-container {
    text-align: right;
    img {
      width: 80%;
      margin: auto;
    }
  }
}

.container {
  height: 100vh;
  margin: auto;
  padding-top: 5%;
  h2 {
    color: white;
    font-size: 25px;
    margin: 0;
  }
  h1#landing-name {
    color: white;
    padding: 0;
    margin: 10px 0px;
    font-size: 60px;
  }
  h3 {
    color: white;
    font-weight: normal;
    font-size: 20px;
    margin: 0px 0px 30px 0px;
    .chip {
      font-size: 16px;
      margin-left: 5px;
      margin-right: 5px;
      border-bottom: 3px solid $main-blue;
    }
  }

  img#photo {
    border-radius: 100%;
    border-right: 6px solid $main-blue;
    border-bottom: 6px solid $main-blue;
    box-shadow: 10px 10px 27px 5px rgba(95, 147, 199, 0.43);
  }

  .resume-btn-container {
    width: 40%;
    button#resume-btn {
      color: white;
      border-radius: 20px 10px 20px 10px;
      background-color: $main-blue;
      border-bottom: 4px solid rgb(47, 96, 157);
    }
  }
}

.social-networks {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: black;

  #contact-me-btn {
    color: rgb(199, 199, 199);
    border: none;
  }
  #contact-me-btn:hover {
    color: white;
    border: none;
    background: transparent;
  }
  img {
    min-width: 22px;
  }
}

.csnackbar {
  button.snack-message {
    color: #fff;
    margin: 0;
    padding: 0;
    font-size: 16px;
    .snack-icon,
    span.snack-color {
      color: $main-blue;
      margin-left: 5px;
      font-weight: bold;
    }
    span.snack-message-span {
      color: #fff;
      font-size: 16px;
      text-transform: lowercase;
    }
  }
}

.cdialog {
  h4 {
    margin: 0;
    color: #fff;
  }
  img {
    border-radius: 40px 40px 40px 40px;
  }
  nav {
    padding: 0;
    margin: 0;
    ul.contact-list {
      padding: 0;
      margin: 0;
      list-style: none;
      li {
        margin: 0;
        h5 {
          padding: 0;
          margin-bottom: 4px;
        }
      }
    }
  }
}
