@import "../../assets/scss/variables";

@media screen and (min-width: 600px) and (max-width: 899px) {
  .bio-container {
    .bio-grid {
      .bio-grid-myself {
        .about-info {
          padding: 0px 30px !important;
        }
      }
    }
  }
}

@media screen and (min-width: 600px) and (max-width: 650px) {
  .bio-container {
    .bio-grid {
      .bio-grid-myself {
        .about-info {
          font-size: 14px;
          padding: 0px 30px !important;
        }
      }
    }
  }
}

.bio-container {
  margin-bottom: 100px;
  .bio-grid {
    .bio-grid-myself {
      img {
        border-radius: 4px;
      }
      p {
        display: block;
        margin: 0;
        padding: 0;
        text-align: left;
        b {
          color: $main-blue;
        }
      }
    }
    .bio-grid-about,
    .bio-grid-updates {
      margin-bottom: 30px;
      h4 {
        font-size: 22px;
        margin: 0;
        padding: 0;
      }
      small {
        color: $main-blue;
      }
    }

    .bio-grid-about {
      .paper-about {
        margin-bottom: 30px;
      }
      p {
        b {
          color: $main-blue;
        }
      }
    }

    .bio-grid-updates {
      .last-updates-container {
        width: 100%;
        min-height: 100px;
        max-height: 500px;
        margin-top: 20px;
        overflow-y: scroll;
      }
    }
  }
}
